import Home from './pages/Home/Home';
import LazyLoadPage from './pages/LazyLoadPage/LazyLoadPage';

export default [
  {
    title: 'Home',
    path: '/',
    component: Home,
  },
  {
    title: 'Lazyload ',
    path: '/LazyLoadPage',
    component: LazyLoadPage,
  },
]



